import { Component } from "react";
import { connect } from 'react-redux';
import { register } from './store/actions'
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
// import FormControlLabel from '@mui/material/FormControlLabel';
// import Checkbox from '@mui/material/Checkbox';
// import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';




// function Copyright(props) {
//     return (
//         <Typography variant="body2" color="text.secondary" align="center" {...props}>
//             {'Copyright ¬© '}
//             <Link color="inherit" href="https://mui.com/">
//                 Your Website
//             </Link>{' '}
//             {new Date().getFullYear()}
//             {'.'}
//         </Typography>
//     );
// }


class Signup extends Component {

    constructor() {
        super();
        this.state = {
            error_text: null
        }
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const datas = {}
        datas.name = data.get('Name')
        datas.email = data.get('email')
        datas.password = data.get('password')
        this.props.dispatch(register(this, datas))
    };

    // componentDidMount() {
    //     this.props.dispatch(apicall())
    // }

    render() {

        return (
            // <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up
                    </Typography>

                    {this.state.error_text === false ? <h3 style={{ color: "green" }}>{this.props.value.message}</h3> : <h3 style={{ color: "red" }}>{this.state.error_text}</h3>}
                    <Box component="form" noValidate onSubmit={this.handleSubmit} sx={{ mt: 3 }}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <TextField
                                    autoComplete="given-name"
                                    name="Name"
                                    required
                                    fullWidth
                                    id="Name" //take out value using id attribute
                                    label="Name"
                                    autoFocus
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Address"
                                    name="email"
                                    autoComplete="email"
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="new-password"
                                />
                            </Grid>

                            {/* <Grid item xs={12}>
                                    <FormControlLabel
                                        control={<Checkbox value="allowExtraEmails" color="primary" />}
                                        label="I accept terms and Conditions which apply.T&C."
                                    />
                                </Grid> */}
                        </Grid>

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        >
                            Sign Up
                        </Button>


                        <Grid container justifyContent="flex-end">
                            <Grid item>
                                <center>
                                    <Typography style={{ cursor: "pointer" }} onClick={() => this.props.history.push("/")}>Login üöÄ </Typography>
                                </center>
                            </Grid>
                        </Grid>
                    </Box>
                </Box>

                {/* <Copyright sx={{ mt: 5 }} /> */}

            </Container >
            // </ThemeProvider >

        );
    }
}

const mapStateToProps = (state) => {
    return {
        value: state.loggedReducer.q
    }
}

export default connect(
    mapStateToProps
)(Signup);


